@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&family=Montserrat:wght@300;400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html {
  font-size: 10px;
}

.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* background: linear-gradient(#121212, #212b46); */
  background: linear-gradient(#fc3153, #901a2d);
}

.title {
  font-family: 'Luckiest Guy', cursive;
  text-transform: uppercase;
  color: #fff;
  font-size: 9rem;
  -webkit-animation: flipTitle 1s linear;
          animation: flipTitle 1s linear;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 0px -6px 0 #212121, 0px -6px 0 #212121, 0px 6px 0 #212121,
    0px 6px 0 #212121, -6px 0px 0 #212121, 6px 0px 0 #212121, -6px 0px 0 #212121,
    6px 0px 0 #212121, -6px -6px 0 #212121, 6px -6px 0 #212121,
    -6px 6px 0 #212121, 6px 6px 0 #212121, -6px 18px 0 #212121,
    0px 18px 0 #212121, 6px 18px 0 #212121, 0 19px 1px rgba(0, 0, 0, 0.1),
    0 0 6px rgba(0, 0, 0, 0.1), 0 6px 3px rgba(0, 0, 0, 0.3),
    0 12px 6px rgba(0, 0, 0, 0.2), 0 18px 18px rgba(0, 0, 0, 0.25),
    0 24px 24px rgba(0, 0, 0, 0.2), 0 36px 36px rgba(0, 0, 0, 0.15);
}

span {
  font-family: 'Montserrat', sans-serif;
}

@-webkit-keyframes flipTitle {
  from {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
  to {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@keyframes flipTitle {
  from {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
  to {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

.containerInput {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 2rem;
  margin: 3.5rem 0;
  display: flex;
  border-radius: 1.5rem;
  box-shadow: 0.1rem 0.3rem 0.8rem rgba(0, 0, 0, 0.4);
}

.containerInput input {
  background-color: transparent;
  border: 0;
  font-size: 2rem;
  color: #fff;
  outline: none;
  margin-right: 0.8rem;
}

.containerInput input::-webkit-input-placeholder {
  color: #fff;
}

.containerInput input:-ms-input-placeholder {
  color: #fff;
}

.containerInput input::placeholder {
  color: #fff;
}

.buttonSearch {
  background-color: transparent;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.buttonSearch:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  width: 35rem;
  border-radius: 1.5rem;
  padding: 1.5rem;
}

main h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

main span {
  font-size: 1.6rem;
  margin-bottom: 1rem;
}

@media (max-width: 700px) {
  .title {
    font-size: 7rem;
  }
}

@media (max-width: 543px) {
  .title {
    font-size: 3.9rem;
    text-shadow: 0.1rem 1rem 0 rgb(0, 0, 0, 1);
  }
}

